:root {
  --background: #0f0f13;
  --foreground: #e0e0e0;
  --terminal-bg: rgba(10, 10, 15, 0.75);
  --terminal-border: rgba(80, 200, 120, 0.3);
  --terminal-glow: rgba(80, 200, 120, 0.2);
  --accent-primary: #50c878;
  --accent-secondary: #7b68ee;
}
@media (prefers-color-scheme: dark) {
  :root {
    --background: #0f0f13;
    --foreground: #e0e0e0;
  }
}
body {
  background: transparent;
  color: var(--foreground);
  font-family: var(--font-mono, monospace);
}
.hyperland-bg {
  display: none;
}
.bg-element {
  display: none;
}
.terminal-window {
  background: var(--terminal-bg);
  border: 1px solid var(--terminal-border);
  box-shadow: 0 0 20px var(--terminal-glow);
  backdrop-filter: blur(10px);
}
.terminal-content {
  font-family: var(--font-mono, monospace);
  line-height: 1.4;
}
.terminal-window.maximized {
  border-color: var(--accent-primary);
  z-index: 100;
}
.terminal-window.maximized .terminal-content {
  scrollbar-width: thin;
}
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes slideUp {
  0% {
    opacity: 0;
    transform: translateY(20px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
.animate-fadeIn {
  animation: fadeIn 0.8s ease-out forwards;
}
.animate-slideUp {
  animation: slideUp 0.5s ease-out forwards;
}
.delay-150 {
  animation-delay: 150ms;
}
.delay-300 {
  animation-delay: 300ms;
}
.delay-450 {
  animation-delay: 450ms;
}
.delay-500 {
  animation-delay: 500ms;
}
.delay-600 {
  animation-delay: 600ms;
}
.pulse-glow {
  animation: pulse-glow 2s infinite alternate;
}
@keyframes pulse-glow {
  0% {
    box-shadow: 0 0 5px rgba(80, 200, 120, 0.5);
  }
  100% {
    box-shadow: 0 0 15px rgba(80, 200, 120, 0.8), 0 0 25px rgba(80, 200, 120, 0.3);
  }
}
@keyframes float {
  0% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-8px);
  }
  100% {
    transform: translateY(0px);
  }
}
.animate-float {
  animation: float 3s ease-in-out infinite;
}
@keyframes scanning {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(100%);
  }
}
.animate-scanning {
  animation: scanning 2s ease-in-out infinite;
}
@keyframes terminal-pulse {
  0% {
    box-shadow: 0 0 20px rgba(80, 200, 120, 0.3);
  }
  50% {
    box-shadow: 0 0 30px rgba(80, 200, 120, 0.5);
  }
  100% {
    box-shadow: 0 0 20px rgba(80, 200, 120, 0.3);
  }
}
.terminal-window.maximized {
  animation: terminal-pulse 3s infinite ease-in-out;
}
.crypter-banner {
  background: linear-gradient(90deg,rgb(250, 17, 17),rgb(255, 255, 255),rgb(0, 0, 255));
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  display: inline-block;
  font-weight: bold;
  margin: 0;
  padding: 0;
  font-family: monospace;
  white-space: pre;
}
.terminal-scrollbar::-webkit-scrollbar {
  width: 8px;
}
.terminal-scrollbar::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0.2);
  border-radius: 4px;
}
.terminal-scrollbar::-webkit-scrollbar-thumb {
  background: var(--accent-primary);
  border-radius: 4px;
}
.terminal-boot {
  animation: terminal-boot 0.8s ease-in-out;
}
@keyframes terminal-boot {
  0% {
    opacity: 0.3;
  }
  30% {
    opacity: 1;
  }
  40% {
    opacity: 0.7;
  }
  50% {
    opacity: 1;
  }
  60% {
    opacity: 0.8;
  }
  100% {
    opacity: 1;
  }
}
.boot-sequence {
  display: block;
  margin-bottom: 6px;
  padding: 4px 0;
  border-left: 3px solid rgba(80, 200, 120, 0.4);
  padding-left: 10px;
  position: relative;
}
.boot-animate {
  animation: boot-fade-in 0.5s ease-out forwards;
}
.boot-sequence::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1px;
  background: linear-gradient(to right, rgba(80, 200, 120, 0.4), transparent);
}
.boot-icon {
  display: inline-block;
  margin-right: 8px;
  animation: pulse 1.5s infinite alternate;
}
.static-icon {
  animation: none !important;
  opacity: 1 !important;
}
.boot-status {
  font-weight: bold;
  color: var(--accent-primary);
  margin-right: 8px;
}
@keyframes boot-fade-in {
  0% {
    opacity: 0;
    transform: translateY(10px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes pulse {
  0% {
    opacity: 0.7;
  }
  100% {
    opacity: 1;
  }
}
.boot-sequence.success {
  border-left-color: rgba(0, 255, 0, 0.6);
}
.boot-sequence.success .boot-status {
  color: #00ff00;
}
.boot-sequence.warning {
  border-left-color: rgba(255, 165, 0, 0.6);
}
.boot-sequence.warning .boot-status {
  color: #ffa500;
}
.boot-sequence.info {
  border-left-color: rgba(0, 191, 255, 0.6);
}
.boot-sequence.info .boot-status {
  color: #00bfff;
}
